    .section-angle {
      position: relative;
      border-color: inherit;
      background: #000000;
    }
    .section-angle:before,
    .section-angle:after {
      width: 0;
      height: 0;
      position: absolute;
      content: "";
      left: 0;
      border: 0 solid transparent;
      z-index: 3;
    }
    .section-angle.top-left:before,
    .section-angle.top-right:before {
      top: 0;
      border-left-width: 100vw;
    }
    .section-angle.bottom-left:after,
    .section-angle.bottom-right:after {
      bottom: 0;
      border-right-width: 100vw;
    }
    .section-angle.bottom-left:after {
      border-right-color: inherit;
    }
    .section-angle.bottom-right:after {
      border-bottom-color: inherit;
    }
    .section-angle.top-left:before {
      border-top-color: inherit;
    }
    .section-angle.top-right:before {
      border-left-color: inherit;
    }
    @media (min-width: 992px) {
      .section-angle.bottom-right:after,
      .section-angle.top-right:before {
        border-bottom-width: 2rem;
      }
      .section-angle.bottom-left:after,
      .section-angle.top-left:before {
        border-top-width: 2rem;
      }
    }
    @media (max-width: 767.98px) {
      .section-angle.bottom-right:after,
      .section-angle.top-right:before {
        border-bottom-width: 1rem;
      }
      .section-angle.bottom-left:after,
      .section-angle.top-left:before {
        border-top-width: 1rem;
      }
    }
    
    /*smart scrolling*/
    .smart-scroll {
      position: fixed;
      top: 0;
      z-index: 1020;
      width: 100vw;
      transition: all 0.3s ease-out;
    }
    .smart-scroll.scrolling {
      -webkit-transform: translateY(-100%);
      transform: translateY(-100%);
    }
    .smart-scroll.scrolling.up {
      background-color: rgba(0, 0, 0, 0.9);
      -webkit-transform: translateY(0);
      transform: translateY(0);
      transition: all 0.3s ease-out;
    }
    
    /**dividers */
    .divider {
      position: relative;
    }
    .divider.top-divider:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      display: block;
      height: 1px;
      background: #263238;
      background: linear-gradient(
        to right,
        rgba(29, 233, 182, 0.1) 0,
        #263238 50%,
        rgba(29, 233, 182, 0.1) 100%
      );
    }
    .divider.bottom-divider:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      display: block;
      height: 1px;
      background: #263238;
      background: linear-gradient(
        to right,
        rgba(29, 233, 182, 0.24) 0,
        #263238 50%,
        rgba(29, 233, 182, 0.24) 100%
